// √âtape 5 : D√©finition d'un √©l√©ment web personnalis√© pour repr√©senter un artiste

class ArtistCover extends HTMLElement {
  connectedCallback() {
    const newContent = document.querySelector('#artist-list-item-template')

    // Clone le contenu du template pour cr√©er une nouvelle instance de cet √©l√©ment.
    // `true` indique que le clonage doit inclure aussi les descendants du n≈ìud, donc toute la structure interne du template.
    const newElement = newContent.content.cloneNode(true)

    // D√©finit le lien (href) de l'√©l√©ment <a> dans le nouvel √©l√©ment clon√© en utilisant l'attribut 'href' de l'√©l√©ment personnalis√©.
    newElement.querySelector('a').href = this.getAttribute('href')
    newElement.querySelector('img').src = this.getAttribute('cover')
    newElement.querySelector('div').innerText = this.getAttribute('name')

    // Remplace les enfants actuels de l'√©l√©ment personnalis√© par le nouvel √©l√©ment clon√©.
    // Cela permet d'afficher le contenu correct de l'artiste dans le DOM.
    this.replaceChildren(newElement)
  }
}

// Enregistre l'√©l√©ment personnalis√© 'artist-cover' aupr√®s du navigateur, en associant la classe ArtistCover √† ce nom.
// Cela permet d'utiliser l'√©l√©ment <artist-cover> dans le HTML.
customElements.define('artist-cover', ArtistCover)
