// √âtape 5 (suite) : D√©finition d'un √©l√©ment web personnalis√© pour repr√©senter une chanson

// Cr√©e un nouvel √©v√©nement personnalis√© 'play_click'. Cet √©v√©nement peut √™tre √©cout√© et g√©r√© ailleurs dans l'application
const playClick = new CustomEvent('play_click')
const favoriteClick = new CustomEvent('favorite_click')

class SongItem extends HTMLElement {

  static observedAttributes = ['favorite'];

  connectedCallback() {
    this.render();
  }

  attributeChangedCallback() {
    this.render();
  }

  render() {
    // D√©finit le HTML interne de l'√©l√©ment. 
    // Utilise le mod√®le de litt√©raux de gabarit pour ins√©rer dynamiquement l'attribut 'title' de l'√©l√©ment dans le HTML.
    const favoriteIcon = this.getAttribute('favorite') == 'true' ? 'favorite' : 'favorite_border';

    this.innerHTML =
      `<a href="#">
      <div class="list-item-title">${this.getAttribute('title')}</div>
      <div class="list-item-actions">
        <button type="button" class="icon-button favorite-button ">
          <span class="material-icons">${favoriteIcon}</span>
        </button>
        <button type="button" class="icon-button play-button">
          <span class="material-icons">play_arrow</span>
        </button>
      </div>
    </a>`

    // Ajoute un √©couteur d'√©v√©nements sur le bouton de lecture. Lorsque ce bouton est cliqu√©, l'√©v√©nement par d√©faut 
    // est emp√™ch√© et l'√©v√©nement personnalis√© 'play_click' est d√©clench√© sur l'√©l√©ment.
    this.querySelector('.play-button').addEventListener('click', (e) => {
      e.preventDefault() // Emp√™che l'action par d√©faut (par exemple, suivre le lien).
      this.dispatchEvent(playClick) // D√©clenche l'√©v√©nement 'play_click' sur cet √©l√©ment.
    })

    this.querySelector('.favorite-button').addEventListener('click', (e) => {
      e.preventDefault()
      this.dispatchEvent(favoriteClick)
    })
  }
}

customElements.define('song-item', SongItem)
